<template>
  <div id="food" class="subPage">
    <header :style="renderPicture">
      <div id="closeBox" class="back" @click="back">
        <img src="static/assets/close.png" alt="close">
      </div>
    </header>
    <div class="detail">
      <p class="single-wrap">{{foodInfo.name}}</p>
      <p>月售721份</p>
      <p>好评率92%</p>
      <p>￥ {{foodInfo.price}}</p>
      <div id="cartBox">
        <img src="static/assets/cart.png" alt="buy">
      </div>
    </div>
    <div id="feedback">
      <div>
        <p>评价</p>
        <p>{{totalAverage}}</p>
      </div>
      <div v-for="item in remarkList" class="fbItem">
        <div class="fbBasic">
          <img src="static/assets/user2.png" alt="user">
          <p class="fbGrade" v-bind:style="colorGrade(item.grade)">{{remarkGrade(item.grade)}}</p>
        </div>
        <div class="fbContent">
          <p>{{item.time.split('T')[0]}}</p>
          <p>{{item.comment}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  export default {
    name: 'food',
    data () {
      return {
        tag: false,
        color: ['#FF5339', '#FFB62D', '#777777'],
        remarkList: []
      }
    },
    methods: {
      back: function () {
        switch (this.$root.previous) {
          case 0:
            this.$router.push({path: '/center/search'})
            break
          case 1:
            this.$router.push({path: '/restaurant/' + this.foodInfo.restaurant_id})
            break
          case 2:
            this.$router.push({path: '/orders'})
            break
        }
      },
      remarkGrade: function (x) {
        if (x >= 4) {
          return '满意'
        } else if (x >= 3) {
          return '一般'
        } else {
          return '差评'
        }
      },
      colorGrade: function (x) {
        if (x >= 4) {
          return 'color: ' + this.color[0]
        } else if (x >= 3) {
          return 'color: ' + this.color[1]
        } else {
          return 'color: ' + this.color[2]
        }
      },
      wrapHeader: function () {
        let self = this
        $('#food').scroll(function () {
          if ($(this).scrollTop() >= 800) {
            self.tag = self.tag ? self.tag : !self.tag
            if (self.tag) {
              $('.detail').addClass('fixed-header')
              $('#feedback').css('margin-top', '332px')
            }
          } else if (self.tag) {
            $('.detail').removeClass('fixed-header')
            $('#feedback').css('margin-top', '0')
            self.tag = false
          }
        })
      }
    },
    computed: {
      foodInfo: function () {
        return this.$store.state.foodInfo
      },
      renderPicture: function () {
        return 'background-image: url(' + this.foodInfo.photo + ');'
      },
      totalAverage: function () {
        let avg
        let sum = 0
        for (let i = 0; i < this.remarkList.length; i++) {
          sum += this.remarkList[i].grade
        }
        avg = sum / this.remarkList.length
        return avg
      }
    },
    mounted () {
      let self = this
      this.wrapHeader()
      $.ajax({
        async: false,
        url: '/get_comments/',
        type: 'POST',
        data: {
          dish_id: self.foodInfo.dish_id,
          // eslint-disable-next-line no-undef
          csrfmiddlewaretoken: csrf_token
        },
        success: function (data) {
          self.remarkList = data
        },
        error: function () {
          console.log('获取评论失败！')
        }
      })
    }
  }
</script>

<style scoped>
  #food{
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: scroll;
    background: white;
    z-index: 4;
  }
  header{
    width: 100%;
    height: 800px;
    background-position: 50% 50%!important;
    background-size: cover!important;
    overflow: hidden;
  }
  .fixed-header{
    position: fixed;
    top: 0;
  }
  #closeBox{
    width: 110px;
    height: 110px;
    margin: 5%;
    border-radius: 8px;
    background: rgba(0, 0, 0, 0.1);
  }
  #closeBox img{
    width: 50px;
    height: 50px;
    margin: 30px;
  }
  .detail{
    width: 100%;
    height: 290px;
    border-bottom: 3px solid #f0f2f5;
    z-index: 2;
    background-color: white;
    padding-top: 40px;
    overflow: hidden;
  }
  .detail p:first-of-type{
    font-size: 60px;
    font-weight: bold;
    margin: 0 0 10px 5%;
  }
  .detail p:nth-of-type(2), .detail p:nth-of-type(3){
    font-size: 36px;
    float: left;
  }
  .detail p:nth-of-type(2){
    margin: 10px 25px 0 5%;
  }
  .detail p:nth-of-type(3){
    margin-top: 10px;
  }
  .detail p:nth-of-type(4){
    margin: 130px 4.5% 0;
    font-size: 54px;
    color: #FF5339;
  }
  #cartBox{
    width: 13%;
    height: 130px;
    float: right;
    position: relative;
    bottom: 103px;
    right: 20px;
  }
  .detail img{
    width: 70px;
    position: relative;
    top: 32px;
    float: right;
    right: calc(38.5% - 20px);
  }
  #feedback{
    width: 90%;
    height: auto;
    margin: 0 5%;
    z-index: 1;
    overflow: hidden;
  }
  #feedback>div:first-of-type{
    width: 100%;
    height: 87px;
    border-bottom: 3px solid #f0f2f5;
  }
  #feedback>div:first-of-type p:first-of-type{
    font-size: 54px;
    font-weight: bold;
    letter-spacing: 4px;
    margin: 30px 0;
  }
  #feedback>div:first-of-type p:nth-of-type(2){
    font-size: 60px;
    color: #FF5339;
    position: relative;
    bottom: 85px;
    left: 140px;
  }
  .fbItem{
    width: 100%;
    height: 300px;
    border-bottom: 2px solid #f0f2f5;
  }
  .fbBasic{
    width: calc(4% + 114px);
    height: 100%;
    float: left;
  }
  .fbItem img{
    width: 114px;
    float: left;
    padding-top: 30px;
  }
  .fbGrade{
    float: left;
    font-size: 40px;
    position: relative;
    top: 16px;
    left: 18px;
  }
  .fbContent{
    width: calc(96% - 114px);
    height: 100%;
    float: left;
    position: relative;
    overflow: hidden;
  }
  .fbContent p:first-of-type{
    padding-top: 33px;
    font-size: 36px;
    padding-bottom: 3px;
  }
  .fbContent p:nth-of-type(2){
    line-height: 170%;
    font-size: 40px;
  }
</style>



